export default {
	data() {
		return {
			payType: 1,
			orderInfo: {},
			goodData:[],
			totalPrice:0,
			orderData:{},
			account:0,
			addressData:{}
		};
	},
	computed: {
	
	},
	onLoad(option) {
		this.loadAccountBalance();
		let data = JSON.parse(option.data).orderTemp;
		this.goodData = data.goodsData;
		this.totalPrice = data.totalPrice;
		this.addressData = data.addressData;
		this.orderData = data;
		this.orderType = data.orderType;
	},

	methods: {
		//选择支付方式
		changePayType(type) {
			this.payType = type;
		},
		//确认支付
		confirm: async function() {
			var that = this;
			var orderInfo = {};
			let goodDetails = [];
			let nums = [];
			let orderType = this.orderType;
			that.goodData.forEach(item => {
				goodDetails.push(item.goodDetailId),
				nums.push(item.num)
			});
			
			orderInfo["orderType"] = orderType;
			orderInfo["goodDetailIds"] = goodDetails;
			orderInfo["nums"] = nums;
			orderInfo["addressId"] = this.addressData.id;
			
			uni.showModal({
				title: '提示',
				content: '确认支付？',
				success: function(res) {
					that.$http.post(that.$httpApi.order.makeOrder, orderInfo).
					then(function(response) {
						uni.redirectTo({
							url: '/pages/money/paySuccess'
						})
					}).catch(function(error) {
						//这里只会在接口是失败状态返回，不需要去处理错误提示
					});
				}
			});
		},
		loadAccountBalance(){
			var that = this;
			this.$http.get(this.$httpApi.user.account, {}).
			then(function(response) {
				//这里只会在接口是成功状态返回
				that.account = response.remain;
			}).catch(function(error) {
				//这里只会在接口是失败状态返回，不需要去处理错误提示
			});
		}
	}
}
